import React, { Component } from 'react';

import { Link } from 'react-router-dom';

import './inventory_selection.scss';
import { withRouter } from 'react-router-dom';
import axios from 'axios';

class inventory_selection extends Component {
	constructor(props) {
		super(props);
		this.state = {
			inventory_master: [],
			inventory_medicines: [],
			term: '',
			pageno: 0,
			pageno_i: 0,
			count_of_unmappped: 0,
			mapped: 'NotMapped',
			inputValues: []
		};
	}

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/total_unmapped/')
			.then((response) => {
				this.setState({ count_of_unmappped: response.data.count });
			})
			.catch((err) => {
				console.log(err);
			});
		axios
			.get('https://medicento-api.herokuapp.com/api/get_unmapped/' + this.state.mapped)
			.then((response) => {
				this.setState({ inventory_medicines: response.data.medicines });
			})
			.catch((err) => {
				console.log(err);
			});
	}

	searchMedicines = (event) => {
		event.preventDefault();
		let term = event.target.value;
		this.setState({ term: term });
	};

	loadMore = () => {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_medicines_of_master/' + this.state.term + '?pagno=' + this.state.pageno)
			.then((response) => {
				this.setState({ inventory_master: response.data.medicines, pageno: this.state.pageno + 1 });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	loadMoreInventory = () => {
		axios
			.get(
				'https://medicento-api.herokuapp.com/api/get_medicines_of_distributor/' +
					this.state.term +
					'?pagno=' +
					this.state.pageno_i
			)
			.then((response) => {
				this.setState({ inventory_medicines: response.data.medicines, pageno_i: this.state.pageno_i + 1 });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	submitSearch = (event) => {
		event.preventDefault();
		axios
			.get(
				'https://medicento-api.herokuapp.com/api/get_medicines_of_distributor/' +
					this.state.term +
					'?pagno=' +
					this.state.pageno_i
			)
			.then((response) => {
				this.setState({
					inventory_medicines: response.data.medicines,
					pageno_i: 1
				});
			})
			.catch((err) => {
				console.log(err);
			});
		axios
			.get('https://medicento-api.herokuapp.com/api/get_medicines_of_master/' + this.state.term + '?pagno=' + this.state.pageno)
			.then((response) => {
				this.setState({ inventory_master: response.data.medicines, pageno: 1 });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	addProductId = (id, event) => {
		event.preventDefault();
		console.log(this.state.inputValues[id]);
		axios
			.get('https://medicento-api.herokuapp.com/api/updateMedicines?mapped_id=' + this.state.inputValues[id])
			.then((response) => {
				console.log(response);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	changeItem = (id, event) => {
		let inputValues = this.state.inputValues;
		inputValues[id] = event.target.value;
		this.setState({ inputValues: inputValues });
	};

	mapped = (event) => {
		let value = event.target.value;
		console.log(value);
		axios
			.get('https://medicento-api.herokuapp.com/api/get_unmapped/' + value)
			.then((response) => {
				this.setState({ inventory_medicines: response.data.medicines, mapped: value });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	render() {
		const inventory_medicines = this.state.inventory_medicines;
		const inventory_master = this.state.inventory_master;

		return (
			<div className="inventory_selection">
				<div class="container" style={{ marginTop: '20px' }}>
					<Link to="inventory_selection">
						<button className="btn btn-primary">Change Distributor</button>
					</Link>
					<h3>Parshva Distributor</h3>
					<div>Total Unmapped - {this.state.count_of_unmappped}</div>
					<form onSubmit={this.submitSearch}>
						<div class="form-group">
							<input
								type="text"
								placeholder="search medicine"
								className="form-control"
								onChange={this.searchMedicines}
							/>
						</div>
						<div class="form-group">
							<input type="submit" value="Search" className="btn btn-primary" />
						</div>
					</form>
					<div className="row">
						<div className="col-md-12">
							<button className="btn btn-primary" onClick={this.loadMore}>
								Next
							</button>{' '}
							Page {this.state.pageno}
							<button className="btn btn-primary" style={{ marginBottom: '10px', marginLeft: '10px' }}>
								Add New
							</button>
							<br />
						</div>
						<table className="table col-md-6">
							<thead>
								<tr>
									<th>Medicento Name</th>
									<th>Add</th>
								</tr>
							</thead>
							<tbody>
								{inventory_master.map((medicines, i) => (
									<tr>
										<td scope="row">{medicines.Item_name}</td>
										<td>
											<form
												action=""
												onSubmit={(e) => {
													this.addProductId(i, e);
												}}
											>
												<input
													type="text"
													name="item_code"
													onChange={(e) => this.changeItem(i, e)}
												/>
												<input
													type="text"
													name="item_id"
													value={medicines._id}
													style={{ display: 'none' }}
												/>
												<input type="submit" value="Submit" className="btn btn-success" />
											</form>
										</td>
									</tr>
								))}
							</tbody>
						</table>
						<div className="col-md-6">
							<select name="mapping" id="" onChange={this.mapped}>
								<option value="Mapped">Mapped</option>
								<option value="NotMapped" selected>
									Un Mapped
								</option>
							</select>
							<button
								className="btn btn-primary"
								onClick={this.loadMoreInventory}
								style={{ marginLeft: '10px' }}
							>
								Next
							</button>{' '}
							Page {this.state.pageno_i}
							<table className="table" style={{ marginTop: '10px' }}>
								<thead>
									<tr>
										<th>Item Code</th>
										<th>Name</th>
										<th>PTR</th>
										<th>Manfacturer</th>
										<th>Status</th>
									</tr>
								</thead>
								<tbody>
									{inventory_medicines.map((medicines, i) => (
										<tr>
											<td>{medicines.item_code}</td>
											<td>{medicines.Item_name}</td>
											<td>{medicines.mrp}</td>
											<td>{medicines.manfc_name}</td>
											<td>{medicines.unmapped}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default withRouter(inventory_selection);
